.input {
  align-items: center;
}

.input label {
  font-weight: bold;
  margin-right: 1rem;
}

.input input {
  width: 5rem;
  border-radius: 5px;
  border: 1px solid var(--secondry-color);
  padding-left: 0.5rem;
}
@media (max-width: 1070px) {
  input,
  .label {
    font-size: 1.5rem;
  }
}
